.btns {
    display: flex;
    justify-content: center;
    align-items: center;
}

.Imagenes {
    border: none;
    background: #146b80;
    padding: 10px;
    color: #ffffff;
    border-radius: 5px;
    margin: 10px;
    font-weight: bold;
    cursor: pointer;
}

